$fa-font-path: "../fonts";

/* Fonts */
$font-body: arial, helvetica, sans-serif;
$font-heading: arial, helvetica, sans-serif;

/* Colour Variables */
$white: #fff;
$light-gray: #eceeef;
$dark-gray: #353535;

$theme-colors: (
	primary: $dark-gray,
);

/* Spacing */
var(--single-gutter): 15px;
var(--double-gutter): var(--single-gutter) * 2;

var(--spacing-bottom): 60px;
var(--spacing-bottom-half): var(--spacing-bottom) / 2;

